/**
 * The following styles were written with the subsequent route in mind,
 * although are not necessarily limited to it.
 *
 * @route  /account/login
 */
.account-login #middle-column {
    .buttons-right {
        margin-right: 2px;
        float: right;

        a, button {
            display: block;
        }
    }

    .information {
        padding-bottom: 4px;
        font-size: 16px;

        .buttons-right {
            margin-top: 19px;
        }

        .indent {
            margin: 0 0 0 10px;
        }
    }
}


/**
 * The following styles were written with the subsequent route in mind,
 * although are not necessarily limited to it.
 *
 * @route  /account/create
 */
.account-create #middle-column {
    label[for="male"] {
        display: inline-block;
        margin-right: 60px;
    }

    p {
        margin: 5px 0;
    }

    button.blue-button {
        margin: 0 auto;
        display: block;
    }
}


/**
 * The following styles were written with the subsequent route in mind,
 * although are not necessarily limited to it.
 *
 * @route  /account/logout
 */
.account-logout #middle-column { }


/**
 * The following styles were written with the subsequent route in mind,
 * although are not necessarily limited to it.
 *
 * @route  /account
 */
.account-index #middle-column {
    .buttons-right {
        margin-right: 2px;
        float: right;

        a, button {
            display: block;
        }

        .blue-button, .green-button {
            margin-bottom: 4px;
        }
    }

    tr.character {
        .play-integer {
            display: block;
        }

        .play-button,
        .character-buttons {
            display: none;
        }

        &.active {
            .play-integer {
                display: none;
            }

            .play-button,
            .character-buttons {
                display: block;
            }
        }
    }
}


/**
 * The following styles were written with the subsequent route in mind,
 * although are not necessarily limited to it.
 *
 * @route  /account/manage
 */
.account-manage #middle-column {
    .buttons-right {
        a, button {
            display: block;
        }

        .green-button {
            margin-bottom: 4px;
        }
    }
}


