html {
    min-width: 1000px;
}

body {
    background: url('@{image-path}/background-artwork.jpg') no-repeat top center #051122;
    font-family: 'lucida grande', tahoma, verdana, arial, sans-serif;
    overflow-y: scroll;
    font-size: 13px;
    margin: 0;
}

a {
    font-family: Verdana, Arial, 'Times New Roman', sans-serif;
    text-decoration: none;
    color: @link-color;
    font-weight: bold;

    &:hover {
        text-decoration: underline;
    }

    img {
        border: none;
    }
}

h1 {
    font-family: Verdana, Arial, 'Times New Roman', sans-serif;
    color: @heading-color;
    text-align: center;
    font-weight: bold;
    font-size: 24px;
    margin: 16px 0;
}

h2 {
    font-family: Verdana, Arial, 'Times New Roman', sans-serif;
    color: @heading-color;
    margin: -6px 0 28px;
    text-align: center;
    font-weight: bold;
    font-size: 17px;

    &:before {
        content: url('@{image-path}/headline-bracer-left.gif');
        position: relative;
        height: 41px;
        width: 46px;
        left: -4px;
        top: 14px;
    }

    &:after {
        content: url('@{image-path}/headline-bracer-right.gif');
        position: relative;
        height: 41px;
        width: 46px;
        right: -4px;
        top: 14px;
    }
}

h4 {
    font-size: 18.6px;
    font-weight: bold;
    margin: 0;
}

label {
    display: inline-block;
}

label, input[type="radio"], input[type="checkbox"] {
    cursor: pointer;
}
